.all-projects-container {
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;
	padding-top: 30px;
	padding-bottom: 30px;
	grid-gap: 30px;
	margin: 0 auto;
	width: 100%;
}

.all-projects-project {
	width: calc((100%  - 30px) / 2);
	box-sizing: border-box;
	
}

@media (max-width: 600px) {
	.all-projects-project {
		width: 90%;
		margin: 0 auto;
	}
	.all-projects-container {
		display: flex;
		flex-wrap: wrap;
		align-items: stretch;
		padding-top: 30px;
		padding-bottom: 30px;
		grid-gap: 16px;
	}
}
