@import "../../../data/styles.css";

.homepage-article {
	width: 90%;
	mix-blend-mode: normal;
	border-radius: 20px;
	opacity: 0.8;
	transition: 0.3s cubic-bezier(0.56, 0, 0.07, 1);
	
}

.homepage-article:hover {
	opacity: 1;
	
}

.homepage-article-content {
	padding: 30px;
	box-shadow: 16px 16px 32px -16px rgba(0, 0, 0, 0.1);
	border-radius: 20px;
}

.homepage-article-date {
	opacity: 1;
	color: var(--tertiary-color);
	font-size: 12px;
	margin-bottom: 1em;
}

.homepage-article-title {
	opacity: 1;
	color: var(--primary-color);
	font-size: 16px;
	margin-bottom: 1em;
	font-weight: 700;
}

.homepage-article-description {
	opacity: 1;
	color: var(--secondary-color);
	font-size: 12px;
	line-height: 1.5;
}

.homepage-article-link {
	opacity: 1;
	padding-top: 20px;
	font-size: 15px;
	font-weight: 700;
}

.homepage-article-link a {
	color: var(--link-color);
	text-decoration: none;
}

@media (max-width: 600px) {
	.homepage-article {
		width: 100% !important;
	}
}

.homepage-articles {
	width: 100% !important;
	transition: 0.3s cubic-bezier(0.56, 0, 0.07, 1);
	margin: 0 auto !important;
}