@import "../../../data/styles.css";

.project {
	mix-blend-mode: normal;
	border-radius: 20px;
	background-position: center center; 
	background-size: 100%;
	opacity: 0.8;
	height: 100%;
	transition: 0.3s cubic-bezier(0.86, 0, 0.07, 1);
	background-repeat: no-repeat;
	height: 338px;
	box-shadow: 12px 16px 32px -16px rgba(0, 0, 0, 0.1);
	text-rendering: geometricPrecision;
}

.project a {
	text-decoration: none;
}

.project:hover {
	background-size: 103%;
	opacity: 1;
	box-shadow: 16px 16px 32px -16px rgba(0, 0, 0, 0.1);
}

.project:hover .project-link {
	color: var(--link-color);
	transition: color 0.3s ease-in-out;
}

.project-container {
	padding: 0px;
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	
	background-color: #2b2b2b00;
	transition: 0.3s cubic-bezier(0.86, 0, 0.07, 1);
	border-radius: 20px;
	overflow: hidden;
	opacity: 0;
}

.project:hover .project-container {
	background-color: #2b2b2b;
	opacity: 1;
}

.project-description
{
	color: var(--secondary-color);
	font-family: var(--secondary-font);
	font-weight: 600;
	font-size: 1rem;
	line-height: 1rem;
	letter-spacing: 2px;
	height: calc(100% / 3);
	transform: translateY(10px);
	width: 90%;
	margin: 0 auto;
	opacity: 0;
	padding: 0px 36px 0px 36px;
	transition: 0.3s cubic-bezier(0.86, 0, 0.07, 1);	
	overflow: hidden;
}

.project:hover .project-description
{
	opacity: 1;
	transition: 0.3s cubic-bezier(0.86, 0, 0.07, 1);	
	transition-delay: 0.5s;
	transform: translateY(0);
}
.project:hover .project-title
{
	opacity: 1;
	transition: 0.3s cubic-bezier(0.86, 0, 0.07, 1);	
	transition-delay: 0.2s;
	transform: translateY(0px);
}

.project-logo {
	width: 30px;
}

.project-logo img {
	width: 100%;
}

.project-title {
	font-family: var(--secondary-font);
	font-weight: 700;
	font-size: 3rem;
	margin: 0 auto;
	opacity: 0;
	color: white;
	width: 90%;
	padding: 36px 36px 8px 36px;
	transition: 0.3s cubic-bezier(0.86, 0, 0.07, 1);
	letter-spacing: 1px;
	transform: translateY(10px);
}



.project-link {
	display: flex;
	align-items: center;
	color: var(--secondary-color);
	font-size: 12px;
	padding-top: 20px;
}

.project-link-icon {
	padding-left: 5px;
	font-size: 13px;
}

.project-link-text {
	padding-left: 20px;
	font-weight: 700;
}


.project-button
{
	color: black !important;
	font-family: var(--secondary-font);
	font-weight: 700;
	width: fit-content;
	letter-spacing: 1px;
	transform: translateY(10px);
	margin: 0 auto;
	opacity: 0;
	margin: 28px 36px 0px 36px;
	padding: 12px 18px;
	transition: 0.3s cubic-bezier(0.86, 0, 0.07, 1);	
	background-color: var(--link-color);
	border-radius: 20px;
}

.project:hover .project-button
{
	opacity: 1;
	transition: 0.3s cubic-bezier(0.86, 0, 0.07, 1);	
	transition-delay: 0.8s;
	transform: translateY(0);
}



@media (max-width: 600px) {
	.project {
		height: 300px;
	}
	.project-title
	{
		font-size: 2rem;
	}
}
